.hidden-menu {
    background-color: #eee;
    width: 300px;
    transform: translateX(-100%);
    position: fixed;
    top: 0;
    bottom: 0;
    padding: 1rem;
    left: 0;
    transition: all .2s ease;
    z-index: 2000;
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
}
.hidden-menu.show-hidden-menu {
	transform: translateX(0);
}

.demo-trigger {
    display: block;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
    text-align: center;
    background-color: #000;
    color: #fff!important;
    z-index: 100;
    opacity: .6;
    transition: opacity .1s ease;
}